/* ==========================================================================
   WooCommerce Product List page styles - begin
   ========================================================================== */

.edgtf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells,
.edgtf-content .woocommerce {
    @include edgtfRelativeHolderLayout();
}

.edgtf-pl-main-holder {
    margin: 0 0 22px;
    
    .edgtf-woo-pl-info-on-image-hover & {
        margin: 0 0 50px;
    }
}

ul.products {
    list-style: none;
	margin: 0 -15px;
    clear: both;
    
    > .product {
        position: relative;
        display: inline-block;
	    vertical-align: middle;
	    float: left;
	    margin: 0 0 13px !important;
	    padding: 0 15px;
	    box-sizing: border-box;
        
        .touch & {
            cursor: pointer;
        }
        
        &:hover {
            
            .edgtf-pl-inner {
                
                .edgtf-pl-text {
                    opacity: 1;
                    @include edgtfTransition(opacity .4s ease);
                }
    
                .edgtf-pl-text-inner {
                    opacity: 1;
                    top: 0;
                    @include edgtfTransition(top .3s ease, opacity .3s ease);
                }
            }
            
            .edgtf-product-list-title,
            .edgtf-pl-rating-holder,
            .price {
                @include edgtfTransform(translateY(-5px) translateZ(0));
                @include edgtfTransitionTransform(.3s ease);
                backface-visibility: hidden;
            }
    
            .edgtf-product-list-title {
                transition-delay: .1s;
            }
    
            .edgtf-pl-rating-holder {
                transition-delay: .2s;
            }
            
            .price {
                transition-delay: .3s;
            }
            
        }
    
        .edgtf-product-list-title,
        .edgtf-pl-rating-holder,
        .price {
            @include edgtfTransform(translateY(0) translateZ(0));
            @include edgtfTransitionTransform(.3s ease);
            backface-visibility: hidden;
        }
        
        .edgtf-product-list-title {
            margin: 0;
            transition-delay: .3s;
        }
        
        .edgtf-pl-rating-holder {
            @include edgtfRelativeHolderLayout();
            line-height: 1;
            margin: 10px 0 4px;
            transition-delay: .2s;
        }
        
        .price {
            @include edgtfWooCommercePriceStyle();
            margin: 9px 0 0;
	        font-size: 23px;
	        line-height: 1.521739130434783em;
	        letter-spacing: .07em;
            transition-delay: .1s;

            .amount {

                &:first-child {
                    text-decoration: line-through;
                    color: #989898;
                }

                &:last-child {
                    text-decoration: none;
                    color: inherit;
                }
            }
        }
        
        .button,
        .added_to_cart {
            margin: 25px 0 0;
            @include edgtfButtonOutlineReverseColor();
    
            &:after {
                position: absolute;
                content: '';
                bottom: 0;
                right: 0;
                width: 0;
                height: 3px;
                background-color: currentColor;
                @include edgtfTransition(width .35s ease);
            }

            &:hover {
                @include edgtfButtonOutlineReverseColor();
                
                &:after {
                    right: auto;
                    left: 0;
                    width: 100%;
                }
            }
    
            .edgtf-woo-pl-info-below-image & {
                margin: 0;
            }
        }
        
        .button.added {
            display: none;
        }
        
        .edgtf-pl-inner {
            @include edgtfRelativeHolderLayout();
            overflow: hidden;
            
            .edgtf-pl-image {
                @include edgtfRelativeHolderLayout();
                overflow: hidden;
                
                img {
                    display: block;
                    width: 100%;
                }
            }
            
            .edgtf-pl-text {
                @include edgtfAbsoluteHolderLayout();
                padding: 10px;
                opacity: 0;
                background-color: rgba($default-heading-color, 0.6);
                box-sizing: border-box;
                @include edgtfTransition(opacity .4s ease .4s);
            }
            
            .edgtf-pl-text-outer {
                @include edgtfTableLayout();
                text-align: center;
            }
            
            .edgtf-pl-text-inner {
                opacity: 0;
                top: 10px;
                @include edgtfTableCellLayout();
                @include edgtfTransition(top .3s ease .4s, opacity .3s ease .4s);
                
                .edgtf-product-list-title,
                .price,
                .star-rating {
                    color: #fff;
                }
                
                .price del {
                    color: rgba(#fff, .8);
                }
            }
            
            > a {
                @include edgtfAbsoluteHolderLayout();
            }
        }
        
        .edgtf-pl-text-wrapper {
            @include edgtfRelativeHolderLayout();
            margin: 22px 0 32px;
            text-align: center;
        }
    }
}

$columns_space_label: ('no', 'tiny', 'small', 'normal', 'medium', 'large');
$columns_space_width: (0, 5, 10, 15, 20, 25);

@for $i from 0 to length($columns_space_label) {
    &.edgtf-woo-#{nth($columns_space_label,$i+1)}-space {
        $column_width: nth($columns_space_width,$i+1);
        $column_width_margin: $column_width * 2;
        
        ul.products {
            @if($column_width == 0) {
                margin: 0;
            } @else {
                margin: 0 -#{$column_width}px;
            }
            
            > .product {
                padding: 0 #{$column_width}px;
                margin: 0 0 #{$column_width_margin}px;
            }
        }
    }
}

.edgtf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells {
    
    $product_list_columns: ('columns-3', 'columns-4');
    
    @for $i from 0 to length($product_list_columns) {
        $column_width: 100%/($i+3);
        
        > .products {
            
            > .product {
                
                @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                    
                    .edgtf-woocommerce-#{nth($product_list_columns,$i+1)} & {
                        width: $column_width;
                        
                        &:nth-child(#{$i+3}n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }
    }
}

.woocommerce {
    
    &.columns-1 {
        
        > .products {
            
            > .product {
                float: none;
                width: 100%;
            }
        }
    }
    
    $product_list_vc_columns: ('columns-2', 'columns-3', 'columns-4', 'columns-5', 'columns-6');
    
    @for $i from 0 to length($product_list_vc_columns) {
        &.#{nth($product_list_vc_columns,$i+1)} {
            $column_vc_width: 100%/($i+2);
            
            > .products {
                
                > .product {
                    width: $column_vc_width;
                    $product_list_vc_columns_label: #{nth($product_list_vc_columns,$i+1)};
                    
                    @if $product_list_vc_columns_label == 'columns-5' {
                        @media only screen and (min-width: $laptop-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-6' {
                        @media only screen and (min-width: $laptop-landscape-large-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-3' {
                        @media only screen and (min-width: $ipad-portrait-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-2' {
                        @media only screen and (min-width: $phone-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else {
                        @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Product List page styles - end
   ========================================================================== */