/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.edgtf-woo-single-page {
	
	&.edgtf-woo-single-thumb-on-left-side {
		
		.edgtf-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {

					figure {
						width: 100%;
						margin: 0;
					}

					.woocommerce-product-gallery__image {

						&:first-child {
							left: 110px;
							width: calc(100% - 110px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: 100px;
								height: calc(25% + 5px);
								padding: 0 0 20px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
							
							$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
							@for $i from 0 to length($thumbnail_numbers) {
								$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
								
								&:nth-child(#{$thumbnail_number_value}#{n}) {
									$first_param: (25 * $i)#{'%'};
									$second_param: (5 * $i)#{px};
									$topPos: calc(#{$first_param} + #{$second_param});
									
									top: $topPos;
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include edgtfRelativeHolderLayout();
	    float: none;
	    margin: 0 0 50px;
    }
        
    .edgtf-single-product-content {
        @include edgtfRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	    padding: 0 0 80px;
	    border-bottom: 1px solid $default-border-color;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 50%;
		    margin: 0;
		    padding: 0 17px 0 0;
		    float: left;
		    box-sizing: border-box;

		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include edgtfRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;

			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .edgtf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 50%;
        padding: 0 0 0 9px;
        float: left;
        box-sizing: border-box;
	
	    .edgtf-single-product-title {
		    display: block;
		    margin: 0 0 20px;
	    }
	
	    .price {
		    @include edgtfWooCommercePriceStyle();
		    font-size: 35px;
		    line-height: 1em;
		    letter-spacing: .07em;

		    .amount {

                &:first-child {
                    text-decoration: line-through;
                    color: #989898;
                }

                &:last-child {
                    text-decoration: none;
                    color: inherit;
                }
            }
	    }
	
	    .woocommerce-product-rating {
		    @include edgtfRelativeHolderLayout();
		    line-height: 1;
		    font-weight: 400;
		    margin: 26px 0 3px;
		
		    .woocommerce-review-link {
			    margin-left: 15px;
			    vertical-align: middle;
			    font-weight: 300;
		    }
	    }
	
	    .woocommerce-product-details__short-description {
		    @include edgtfRelativeHolderLayout();
		    margin: 26px 0 0;
		
		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 31px 0 0;
		
		    > span {
			    display: block;
			    font-weight: 700;
			
			    span,
			    a {
				    color: $default-text-color;
				    font-weight: 300;

				    &:first-child {
					    margin-left: 10px;
				    }

				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	    
	    .cart {
		    
		    a.button {
			    
			    &:hover {
				
				    color: $first-main-color;
				    background-color: transparent;
				    border-color: $first-main-color;
				    
				    &:after {
					    right: auto;
					    left: 0;
					    width: 100%;
				    }
			    }
			    
			    &:after {
				    position: absolute;
				    content: '';
				    bottom: 0;
				    right: 0;
				    width: 0;
				    height: 3px;
				    background-color: currentColor;
				    @include edgtfTransition(width .35s ease);
			    }
		    }
	    }
	
	    .edgtf-woo-social-share-holder {
		    @include edgtfRelativeHolderLayout();
		    margin: 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    p.cart,
	    form.cart {
		    @include edgtfRelativeHolderLayout();
		    margin: 54px 0 0;
	    }
	
	    .edgtf-quantity-buttons {
		    margin: 0 20px 0 0;
	    }
    }
	
	.woocommerce-tabs {
		@include edgtfRelativeHolderLayout();
		margin: 68px 0 0;

		> div {
			box-sizing: border-box;
		}

		ul.tabs {
			@include edgtfRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			box-sizing: border-box;
			text-align: center;
			clear: both;
			
			@include phone-landscape {
				display: block;
			}
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				margin: 0 -3px -1px;
				padding: 0;
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 11px 82px;
					border-bottom: 1px solid rgba(#aaa, .8);
					text-transform: uppercase;
					font-weight: 700;
					letter-spacing: .05em;
					color: #777;
					
					&:after {
						content: '';
						position: absolute;
						left: 0;
						bottom: -2px;
						height: 3px;
						width: 0;
						background-color: currentColor;
						transition: width .3s ease-out;
					}
					&:hover {
						&:after {
							width: 100%;
						}
					}
				}
				
				&.active,
				&:hover {
					
					a {
						color: rgba($default-heading-color, 1);
						&:after {
							width: 100%;
						}
					}
				}
			}
		}

		.entry-content {
			@include edgtfRelativeHolderLayout();
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 47px 0 38px;
			}

			&.woocommerce-Tabs-panel--description {
				padding-top: 51px;
			}
		}
		
		table {
			margin: 59px 0 35px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				width: 1%;
				padding: 0 10px 0 0;
			}
			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include edgtfRelativeHolderLayout();
			margin: 56px 0 48px;
			
			h2 {
				font-size: 23px;
				line-height: 29px;
				letter-spacing: .05em;
				margin: 13px 0 60px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 62px;
					
					&:last-child {
						margin: 0;
					}

					.woocommerce-review__author {
						text-transform: uppercase;
						letter-spacing: .05em;
						color: $default-heading-color;
					}

					.woocommerce-review__dash,
					.woocommerce-review__published-date {
						display: none;
					}
				}
				
				.comment_container {
					@include edgtfRelativeHolderLayout();
					
					img {
						float: left;
						width: 80px;
						border-radius: 100%;
					}
				}
				
				.comment-text {
					padding: 0 0 0 108px;
					
					.star-rating {
						float: right;
						margin: 3px 0 0;
					}

					.description {
						margin: 4px 0 0;
					}

					p {
						margin: 0;
					}
				}
			}
			
			.comment-respond {
				margin-top: 125px;
				
				.comment-reply-title {
					font-size: 18px;
					line-height: 24px;
					font-weight: 700;
					text-transform: uppercase;
					color: $default-heading-color;
					margin: 0 0 10px;
				}
				
				p {
					margin: 0;
				}
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include edgtfWooCommerceSingleProductRatingsStyle();
					}
				}
				
				#commentform {
					margin-top: 55px;

					.comment-notes {
						float: left;
						margin: 0 0 15px;
					}

					.comment-form-rating {
						float: right;
						margin: 1px 0 10px;

						label {
							float: left;
							margin-right: 24px;
							font-size: 12px;
							font-weight: 700;
							letter-spacing: .05em;
							text-transform: uppercase;
							color: $default-heading-color;
						}

						.stars {
							position: relative;
							top: -7px;
							right: -3px;
						}
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 68px 0 0;
		border-top: 1px solid $default-border-color;
		
		> h2 {
			display: block;
			font-size: 23px;
			line-height: 1.521739130434783em;
			margin: 63px 0 58px;
		}
		
		.product {
			
			.edgtf-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */