/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .edgtf-content,
div.woocommerce {

    .edgtf-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        .edgtf-quantity-input,
        .edgtf-quantity-minus,
        .edgtf-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;
            background-color: transparent;
            border: 1px solid $default-border-color;
            text-align: center;
            box-sizing: border-box;
        }

        .edgtf-quantity-input {
            position: relative;
            height: 57px;
            width: 57px;
            padding: 0;
            margin: 0 0 0 -5px;
            font-family: inherit;
            font-size: 20px;
	        font-weight: 700;
            line-height: 57px;
            outline: 0;
        }

        .edgtf-quantity-minus,
        .edgtf-quantity-plus {
            position: relative;
            right: 0;
            width: 57px;
            height: 57px;
            line-height: 57px;
            cursor: pointer;
            @include edgtfTransition(color .15s ease-in-out);

            &:hover {
                color: $first-main-color;
            }
        }

        .edgtf-quantity-plus {
	        left: -5px;
	        border-left: 0;
        }

        .edgtf-quantity-minus {
	        border-right: 0;
        }
    }
}

.woocommerce-page .edgtf-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
    }

    .coupon {

        input[type="text"] {
            width: 204px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */