/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.edgtf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    padding: 0 10px;

    .touch & {
        cursor: pointer;
    }

    .edgtf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .edgtf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;

        .edgtf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: text-top;
        }

        .edgtf-cart-number {
            font-size: 12px;
            font-weight: 400;
        }

        &.edgtf-header-cart-icon-pack {
            
            .edgtf-cart-icon {
                font-size: 14px;
                line-height: 1em;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.edgtf-header-cart-svg-path {
            
            .edgtf-cart-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }

    &:hover {

        .edgtf-shopping-cart-dropdown {
	        opacity: 1;
	        max-height: 1000px;
	        visibility: visible;
        }
    }
}

.edgtf-dark-header .edgtf-page-header > div:not(.edgtf-sticky-header):not(.fixed) .edgtf-shopping-cart-holder .edgtf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.edgtf-light-header .edgtf-page-header > div:not(.edgtf-sticky-header):not(.fixed) .edgtf-shopping-cart-holder .edgtf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.edgtf-shopping-cart-dropdown {
    position: absolute;
    top: 100%;
    left: auto;
    right: 0;
    width: 280px;
    max-height: 0;
    background-color: #151515;
    border: 1px solid #2e2e2e;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    overflow: hidden;
    box-sizing: border-box;
    @include edgtfTransition(max-height 1s ease,visibility .5s ease);

    ul {
        margin: 25px 27px;
        padding: 0;

        li {
            @include edgtfTableLayout();
            padding: 9px 0;
            box-sizing: border-box;

            &.edgtf-empty-cart {
                color: #fff;
                border-bottom: 0;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .edgtf-item-image-holder {
        position: relative;
        top: 2px;
        display: table-cell;
        height: 100%;
        width: 52px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .edgtf-item-info-holder {
        @include edgtfTableCellLayout();
        vertical-align: top;
        padding-left: 19px;

        .edgtf-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            font-size: 10px;
            font-weight: 700;
            line-height: 16px;
            text-transform: uppercase;
            letter-spacing: .35em;
            color: #fff;
            box-sizing: border-box;

            a {
                color: inherit;
            }
        }

        .remove {
            position: absolute;
            top: -2px;
            right: -3px;
	        font-size: 13px;
	        line-height: 1;
            color: #a3a3a3;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .edgtf-quantity {
            display: block;
            margin: 6px 0 0;
            font-size: 13px;
            font-weight: 300;
            line-height: 1;
            color: #d4d4d4;
        }

        .amount {
            display: block;
            margin: 9px 0 0;
            font-size: 13px;
            font-weight: 300;
            line-height: 1;
            text-transform: uppercase;
            color: #d4d4d4;
        }
    }

    .edgtf-cart-bottom {
        margin: 14px 0 0;
        padding: 21px 0 0;
        border-top: 1px solid #454545;

        .edgtf-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 23px;
            font-size: 10px;
            font-weight: 700;
            text-transform: uppercase;
            letter-spacing: .35em;

            > * {
                display: table-cell;
                vertical-align: baseline;
                color: #fff;
                line-height: 13px;
            }

            .edgtf-total-amount {
                text-align: right;
	            font-size: 13px;
                font-weight: 300;
                text-transform: uppercase;
                color: #d4d4d4;
            }
        }

        .edgtf-view-cart {
            @include edgtfRelativeHolderLayout();
            height: 60px;
            line-height: 60px;
            padding: 0 5px;
            font-size: 10px;
            font-weight: 700;
	        letter-spacing: .35em;
            text-align: center;
            color: #fff;
            background-color: #151515;
            border: 2px solid #fff;
            box-sizing: border-box;
            //@include edgtfTransition(color .2s ease-out, background-color .2s ease-out);
            
            &:after {
                position: absolute;
                content: '';
                bottom: 0;
                right: 0;
                width: 0;
                height: 3px;
                background-color: currentColor;
                @include edgtfTransition(width .35s ease);
            }

            span {
                display: block;
            }

            &:hover {
                
                &:after {
                    right: auto;
                    left: 0;
                    width: 100%;
                }
            }
        }
    }
}

.edgtf-vertical-menu-area {

    .edgtf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */