/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin edgtfWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 0;
    width: 59px;
    height: 29px;
    margin: 0;
	padding: 0;
    color: #000;
    font-size: 12px;
	font-weight: 700;
    line-height: 2.916666666666667em;
    letter-spacing: .08em;
    text-align: center;
	text-transform: uppercase;
    z-index: 100;
    box-sizing: border-box;
    @include edgtfTransform(translateZ(0));
}

@mixin edgtfWooCommerceSaleStyle() {
    left: 12px;
    top: 12px;
    background-color: #fff;
}

@mixin edgtfWooCommerceOutOfStockStyle() {
    left: 12px;
    top: 12px;
    background-color: #fff;
}

@mixin edgtfWooCommerceNewProductStyle() {
    left: 12px;
    top: 12px;
    background-color: #fff;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin edgtfWooCommercePriceStyle() {
    position: relative;
    display: block;
    margin: 3px 0 0;
    line-height: 1;
    font-weight: 700;
    color: $default-heading-color;
    
    * {
        font-size: inherit;
        line-height: inherit;
    }
    
    del {
        margin: 0 8px 0 0;
        color: #989898;
    }
    
    ins {
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "Font Awesome 5 Free";
    font-size: 13px;
    line-height: 1;
    letter-spacing: 3px;
    color: #989898;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin edgtfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\f005\f005\f005\f005\f005";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\f005\f005\f005\f005\f005";
	        font-weight: 900;
        }
    }
}

@mixin edgtfWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\f005";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
    
    &.active:after {
        content: "\f005\f005\f005\f005\f005";
	    font-weight: 900;
        position: absolute;
        right: 0;
        bottom: 7px;
        display: block;
        @extend %star-ratings;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */