/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.edgtf-pl-holder {
    @include edgtfRelativeHolderLayout();
	
	&.edgtf-info-on-image {
		
		.edgtf-pli {
			
			.edgtf-pli-add-to-cart {
				margin: 25px 0 0;
			}
		}
	}

    .edgtf-pli {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        padding: 0;
        z-index: 100;
        box-sizing: border-box;
	
	    &:hover {
		    
		    .edgtf-pli-inner {
			    .edgtf-pli-text {
				    opacity: 1;
				    @include edgtfTransition(opacity .4s ease);
			    }
			
			    .edgtf-pli-text-inner {
				    opacity: 1;
				    top: 0;
				    @include edgtfTransition(top .3s ease, opacity .3s ease);
			    }
		    }
		
		    .edgtf-pli-text-wrapper  {
			
			    .edgtf-pli-title,
			    .edgtf-pli-price {
				    @include edgtfTransform(translateY(-5px) translateZ(0));
				    @include edgtfTransitionTransform(.3s ease);
				    backface-visibility: hidden;
			    }
			
			    .edgtf-pli-title {
				    transition-delay: .1s;
			    }
			    
			    .edgtf-pli-price {
				    transition-delay: .2s;
			    }
		    }
	    }

	    .edgtf-pli-title {
		    margin: 0;
	    }
	
	    .edgtf-pli-category {
		    margin: 11px 0 3px;
	    }

        .edgtf-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .edgtf-pli-rating-holder {
		    @include edgtfRelativeHolderLayout();
		    line-height: 1;
		    margin: 10px 0 4px;
	    }

	    .edgtf-pli-rating {
		    @include edgtfWooCommerceRatingsStyle();
	    }

        .edgtf-pli-price {
	        @include edgtfWooCommercePriceStyle();
	        font-size: 23px;
	        line-height: 1.521739130434783em;
	        letter-spacing: .07em;
        }
    }

    .edgtf-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        .touch & {
            cursor: pointer;
        }

        .edgtf-pli-image {
            @include edgtfRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

            .edgtf-pli-onsale,
            .edgtf-pli-out-of-stock,
            .edgtf-pli-new-product {
	            @include edgtfWooCommerceMarksGeneralStyle();
            }

            .edgtf-pli-onsale {
	            @include edgtfWooCommerceSaleStyle();
            }

            .edgtf-pli-out-of-stock {
	            @include edgtfWooCommerceOutOfStockStyle();
            }
	
	        .edgtf-pli-new-product {
		        @include edgtfWooCommerceNewProductStyle();
	        }
        }
            
        .edgtf-pli-text {
            @include edgtfAbsoluteHolderLayout();
            padding: 10px;
            opacity: 0;
            background-color: rgba($default-heading-color, 0.6);
            box-sizing: border-box;
            @include edgtfTransition(opacity .4s ease .3s);
        }

        .edgtf-pli-text-outer {
	        @include edgtfTableLayout();
	        text-align: center;
        }

        .edgtf-pli-text-inner {
	        opacity: 0;
	        top: 10px;
            @include edgtfTableCellLayout();
	        @include edgtfTransition(top .3s ease .3s, opacity .3s ease .3s);

            .edgtf-pli-title,
            .edgtf-pli-category a,
            .edgtf-pli-excerpt,
            .edgtf-pli-rating,
            .edgtf-pli-price {
                color: #fff;
            }
	
	        .edgtf-pli-category a:hover {
		        color: rgba(#fff, .8);
	        }

	        .edgtf-pli-add-to-cart {

		        &.edgtf-default-skin {

			        .button,
			        .added_to_cart {
				        @include edgtfButtonOutlineReverseColor();

			            &:hover {
				            &:after {
					            right: auto;
					            left: 0;
					            width: 100%;
				            }
			            }
			        }
		        }

		        
		        &.edgtf-light-skin {

			        .button,
			        .added_to_cart {
				        @include edgtfButtonOutlineReverseColor();
			        }
		        }
		
		        &.edgtf-dark-skin {
			
			        .button,
			        .added_to_cart {
				        @include edgtfButtonOutlineColor();
			        }
		        }
	        }

            .button,
            .added_to_cart {
	            @include edgtfButtonDefaultStyle();
	            @include edgtfButtonMediumParams();
	            white-space: nowrap;
	            z-index: 3;
	            
	            &:after {
		            position: absolute;
		            content: '';
		            bottom: 0;
		            right: 0;
		            width: 0;
		            height: 3px;
		            background-color: currentColor;
		            @include edgtfTransition(width .35s ease);
	            }
            }

            .button.added {
                display: none;
            }
        }

	    .edgtf-pli-link {
		    @include edgtfAbsoluteHolderLayout();
	    }
    }

    .edgtf-pli-text-wrapper {
        @include edgtfRelativeHolderLayout();
        margin: 19px 0 31px;
        text-align: left;
	    
	    .edgtf-pli-title,
	    .edgtf-pli-price {
		    @include edgtfTransform(translateY(0) translateZ(0));
		    @include edgtfTransitionTransform(.3s ease);
		    backface-visibility: hidden;
	    }
	    .edgtf-pli-title {
		    transition-delay: .2s;
	    }
	
	    .edgtf-pli-price {
		    transition-delay: .1s;
	    }
	    
    }
	
	&.edgtf-product-info-light {
		
		.edgtf-pli-inner {
			
			.edgtf-pli-text-inner {
				
				.edgtf-pli-title,
				.edgtf-pli-category a,
				.edgtf-pli-excerpt,
				.edgtf-pli-rating,
				.edgtf-pli-price {
					color: #fff;
				}
				
				.edgtf-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.edgtf-product-info-dark {
		
		.edgtf-pli-inner {
			
			.edgtf-pli-text-inner {
				
				.edgtf-pli-title,
				.edgtf-pli-category a,
				.edgtf-pli-excerpt,
				.edgtf-pli-rating,
				.edgtf-pli-price {
					color: $default-heading-color;
				}
				
				.edgtf-pli-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}
	
	/*************** Standard Layout - begin ***************/
	
	&.edgtf-standard-layout {
		
		.edgtf-pli {
			float: left;
		}
		
		&.edgtf-large-space {
			margin: 0 0 -50px;
		}
		
		&.edgtf-normal-space {
			margin: 0 0 -30px;
		}
		
		&.edgtf-small-space {
			margin: 0 0 -20px;
		}
		
		&.edgtf-tiny-space {
			margin: 0 0 -10px;
		}
		
		$standard_list_columns: ('two', 'three', 'four', 'five', 'six');
		
		@for $i from 0 to length($standard_list_columns) {
			&.edgtf-#{nth($standard_list_columns,$i+1)}-columns {
				$column_width: 100%/($i+2);
				
				.edgtf-pli {
					width: $column_width;
					
					@media only screen and (min-width: $ipad-landscape-plus-pixel) {
						
						&:nth-child(#{$i+2}n+1) {
							clear: both;
						}
					}
				}
			}
		}
	}
	/*************** Standard Layout - end ***************/
	
	/*************** Masonry Layout - begin ***************/
	
	&.edgtf-masonry-layout {
		
		.edgtf-pl-outer {
			opacity: 0;
		}
		
		.edgtf-pl-sizer {
			width: 100%;
		}
		
		.edgtf-pl-gutter {
			width: 0;
		}
		
		.edgtf-pli-inner {
			width: 100%;
			height: 100%;
			
			.edgtf-pli-image {
				height: 100%;
				
				img {
					height: 100%;
				}
			}
		}
		
		.edgtf-pli.edgtf-woo-image-large-width {
			
			img {
				width: 100%;
			}
		}
		
		$masonry_list_columns: ('two', 'three', 'four', 'five', 'six');
		
		@for $i from 0 to length($masonry_list_columns) {
			&.edgtf-#{nth($masonry_list_columns,$i+1)}-columns {
				$column_width: 100%/($i+2);
				
				.edgtf-pli,
				.edgtf-pl-sizer {
					width: $column_width;
				}
				
				.edgtf-pli.edgtf-woo-image-large-width,
				.edgtf-pli.edgtf-woo-image-large-width-height {
					width: $column_width*2;
				}
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */