/* ==========================================================================
   WooCommerce Result and Ordering styles - begin
   ========================================================================== */
.edgtf-woocommerce-page {

    .woocommerce-result-count,
    .woocommerce-ordering {
        display: inline;
        width: 50%;
        float: left;
    }

    .woocommerce-result-count {
        margin: 0 0 16px;
        line-height: 32px;
    }

    .woocommerce-ordering {
        margin: 0 0 14px;
        text-align: right;
        
        .orderby {
            width: auto;
	        min-width: 230px;
	        margin: 0;
	        text-align: initial;
        }

        .select2-container {

            .select2-selection {
                border: none;

                &.select2-selection--single {
                    height: 34px;
                    line-height: 32px;
                }

                .select2-selection__rendered {
                    padding: 0 14px 0 36px;
                }

                .select2-selection__arrow {
                    top: 1px;
                    right: 4px;

                    &:before {
                        font-family: 'ElegantIcons';
                        font-weight: 400;
                        content: '\33';
                    }
                }
            }
        }
    }

    .select2-container--default {

        &.select2-container--open {

            .select2-dropdown {

                &.select2-dropdown--below {
                    border-top-color: $default-border-color;
                }

                &.select2-dropdown--above {
                    border-bottom-color: $default-border-color;
                }
            }
        }

        .select2-results__options {
            padding: 5px 15px 15px 36px;
        }
    }
}
/* ==========================================================================
   WooCommerce Result and Ordering styles - end
   ========================================================================== */